
.editor {
  ul {
    line-height: 2; }
  .f32 {
    @include margin(40px null); }

  table {
    margin-bottom: 20px;
    tr {
      border-top: 1px solid #dbdbdb; }
    th,
    td {
      @include padding(20px null); }

    th {
      width: 150px;
      font-weight: normal;
      color: $green; } }

  img {
    width: 100%; }

  @include max(1200) {
    .f32 {
      @include margin(30px null); }

    table {
      th,
      td {
        @include padding(16px null); } } }

  @include max(767) {
    .f32 {
      @include margin(15px null); }

    table {
      th,
      td {
        @include padding(14px null); }

      th {
        width: 140px; } } }

  @include max(380) {
    .f32 {
      @include margin(15px null); }

    table {
      th,
      td {
        @include padding(12px null); }

      th {
        width: 90px; } } } }


@include min(769) {
  .container.w90 {
    width: 90%; } }

.product {
  position: relative;
  margin-top: 55px;
  padding-right: 300px; }


.recommand {
  @include position(absolute, 0 50px null null );
  .inner {
    width: 190px; }
  .f18 {
    margin-bottom: 24px; }
  a {
    width: 100%;
    border: 1px solid #ccc;
    margin-bottom: 12px; }

  @include min(1366) {
    &.sticky {
      .outer {
        position: fixed;
        top: 80px; } }
    &.bottom {
      bottom: 0;
      top: auto; } } }


@include max(1365) {
  .product {
    padding-right: 0; }

  .recommand {
    position: static;
    @include padding(20px);
    @include margin(null null 50px null );
    background-color: #eee;
    text-align: center;

    .outer {
      display: inline-block;
      text-align: left;
      width: 100%;
      max-width: 530px; }

    .inner {
      width: 100%;
      @include fc; }

    .f18 {
      @include margin(null null 10px 6px); }
    a {
      @include margin(null 6px 0); } } }

@include max(450) {
  .recommand {
    @include padding(12px 5px 16px);
    a {
      @include margin(null 4px null); } } }



.info-top {
  @include f; }

.slick-container {
  @include fcl;

  margin-right: 60px;
  width: 49%;
  flex-shrink: 0;
  max-width: 540px;

  .slick-show {
    width: 100%;
    margin-bottom: 30px;
    border: 1px solid #ccc; }

  .slick-nav {
    width: 100%;

    img {
      border: 1px solid #ccc;
      @include margin(null 18px null null );
      &.slick-current {
        border-color: $green; } } } }



.summary {
  width: 100%; }

.summary__title {
  text-indent: -15px; }

.summary__sub {
  color: #969696;
  padding-bottom: 28px;
  margin-bottom: 28px;
  border-bottom: 1px solid #ccc; }

.summary__list {}

.summary__tag {
  border-bottom: 1px solid #ccc;
  padding-top: 40px;
  margin-bottom: 40px;
  position: relative;
  .tag {
    @include position(absolute, 0 null null null );
    background-image: url("./images/0c-bgc.svg");
    background-repeat: no-repeat;
    color: #fff;
    @include size(110px, 75px);
    @include fc;
    padding-bottom: 15px; } }

.summary__price {}




.price-origin {
  font-size: 36px;
  color: #969696;
  font-family: "Roboto";
  text-decoration: line-through; }
.price-origin + span {
  @include margin(null 10px); }


.price-discount {
  font-size: 50px;
  color: $green;
  font-family: "Roboto"; }

.summary__btn {
  @include f;
  height: 70px;
  max-width: 440px; }

.summary__select,
.summary__add {
  width: calc(50% - 6px);
  height: 100%;
  background-color: $green;
  color: #fff;
  border-radius: 10px;
  font-size: 20px;
  white-space: nowrap;
  img {
    width: auto; } }

.summary__select {
  margin-right: 12px;

  position: relative;
  overflow: hidden;
  @include padding(null 25px);

  .wrap {
    position: relative;
    @include fc(flex-start);
    @include size(100%);
    pointer-events: none; }

  select {
    background-color: transparent;
    border-width: 0px;
    color: #fff;
    @include size(100%);

    padding-left: 50%;
    text-align: center !important;
    @include position(absolute, 0 null null 0);

    option {
      color: $green; }


    @include trans;
    &::-ms-expand {
      display: none; }
    &:hover {
      background-color: $greenL;
      option {
        background-color: #fff; } }
    // &:focus
 }    // &::placeholder


  [class*="icon"]:before {
    // color: #fff
    @include position(absolute, 50% 0 null null );
    transform: translateY(-50%);
    pointer-events: none; } }



.summary__add {
  @include fc;
  cursor: pointer;
  img {
    margin-right: 15px; } }


@include max(1200) {
  .slick-container {
    margin-right: 40px;
    width: 37%;
    .slick-nav img {
      @include margin(null 12px null null ); } }

  .summary__sub {
    padding-bottom: 18px;
    margin-bottom: 18px; }

  .summary__select,
  .summary__add {
    font-size: 18px; }
  .summary__add img {
    width: 20px; }

  .summary__btn {
    height: 55px; }

  .price-origin {
    font-size: 24px; }
  .price-discount {
    font-size: 36px; } }


@include max(767) {
  .product {
    margin-top: 30px; }

  .info-top {
    flex-direction: column;
    align-items: center;
    max-width: 500px;
    @include margin(null auto); }

  .slick-container {
    @include margin(null 0 30px null );
    width: 60%;
    .slick-show {
      margin-bottom: 15px; } }

  .summary__list {
    font-size: 15px; }

  .summary__tag {
    margin-bottom: 20px;
    margin-top: -10px; }

  .summary__btn {
    max-width: 400px; } }
@include max(400) {
  .slick-container {
    width: 80%; }

  .summary__list {
    font-size: 13px; }

  .summary__note {
    font-size: 14px; }

  .summary__btn {
    height: 45px; }

  .summary__select,
  .summary__add {
    font-size: 14px; }

  .summary__add img {
    width: 16px;
    margin-right: 5px; } }



.tab__nav {
  @include f;
  border-bottom: 1px solid #dbdbdb;

  .tab {
    background-color: #dbdbdb;
    @include size(180px, 60px);
    @include fc;
    margin-right: 40px;
    border-radius: 99px;
    position: relative;
    cursor: pointer;
    white-space: nowrap;

    &:before {
      margin-right: 10px;
      font-size: 25px; }

    &:after {
      content: "";
      display: block;
      @include position(absolute, null null -26px null );
      @include size(0);
      border-style: solid;
      border-width: 26px 20px 0 10px;
      border-color: #dbdbdb transparent transparent transparent; }

    &:hover,
    &.active {
      background-color: $green;
      color: #fff;
      &:before {
        color: #FFF; }
      &:after {
        border-top-color: $green; } } } }

.tab__content {
  margin-bottom: 110px; }

.tab__nav {
  @include max(767) {
    @include margin(null -6px );

    .tab {
      @include size(140px, 45px);
      @include margin(null 6px );
      border-radius: 15px;

      &:after {
        @include position(absolute, null null -12px null ); }

      &:before {
        margin-right: 6px;
        font-size: 18px; } } }

  @include max(380) {
    .tab {
      &:before {
        display: none; } } } }
