

.lity-image img {
  width: 80vw;
  max-width: 1000px;
  max-height: 100% !important;
  @include max(500) {
    width: 95vw; } }

.lity-container {
  overflow-y: auto; }


#fixed-cover {
  z-index: 5;
  @include size(100%, 100vh);
  @include position(fixed, 0 0 0 0);
  background-color: rgba(black, 0.3);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.4s, opacity 0.4s linear;
  &.show {
    visibility: visible;
    opacity: 1;
    transition: visibility 0.4s, opacity 0.4s linear; } }


