.check_unit {
  cursor: pointer;
  @include f(null, center, row);
  position: relative;

  input {
    display: none; }

  .box {
    display: inline-block;
    @include size(16px);
    background: #fff;
    border: 2px solid #959595;
    margin-right: 10px;
    border-radius: 5px;
    flex-shrink: 0;
    box-shadow: none;

    &:after {
      content: "";
      position: absolute;
      @include size(0, 2px);
      background: #fff;
      transform: translate(5px, 8px) rotate(-45deg);
      transform-origin: left;
      transition: all 50ms;
      transition-delay: 0ms;
 }      // border-radius: 20% 60% 60% 20%

    &:before {
      content: "";
      position: absolute;
      @include size(0, 2px);
      background: #fff;

      transform: translate(2px, 4px) rotate(45deg);
      transform-origin: left;
      transition: all 50ms;
      transition-delay: 50ms;
 } }      // border-radius: 60%

  input:checked {
    & + .box {
      background-color: $green;
      border-color: $green;
      &:before {
        width: 6px;
        transition-delay: 0ms; }
      &:after {
        width: 8px;
        transition-delay: 50ms; } } }

  input:disabled:not(:checked) {
    & + .box {
      background: #eee;
      border-color: #ccc; }
    & ~ * {
      cursor: default;
      color: #999; } }

  input:checked:disabled {
    & + .box {
      opacity: 0.6; } } }


