
.advanced {
  .icon-scan {
    border: 0;
    cursor: pointer;
    display: flex;
    @include size(200px, 60px);
    @include margin(null auto);
    border-radius: 15px;
    @include hoverBgc($greenL);
    &:before {
      margin-right: 10px;
      font-size: 22px; } } }


.search__block {
  background-color: #efefef;
  @include f;
  border-radius: 10px;
  font-weight: bold; }

.search__title {
  @include padding(null 22px);
  width: 140px;
  flex-shrink: 0;
  @include padding(18px null); }

.search__content {
  @include f;
  flex-wrap: wrap;
  position: relative;

  .check_unit,
  .radio_unit {
    margin-right: 18px;
    @include padding(18px null);
    @include hoverText($green); }

  .radio_unit {
    label:before {
      margin-right: 10px;
      @include size(16px);
      border: 2px solid #9b9b9b; }

    input:checked + label:before {
      background-color: $green;
      border-color: $green; } }


  .multi-border {
    pointer-events: none;
    position: absolute;
    @include size(100%, 99%);
    overflow: hidden;
    .border {
      position: absolute;
      content: "";
      display: block;
      @include size(calc(100% - 20px), 1px);
      background-color: #ccc; }

    @for $var from 1 through 6 {
      .border:nth-child(#{$var}) {
        top: #{$var*60}px; } } } }

@include max(767) {
  .search__title {
    @include padding(null null 10px null ); }

  .search__block {
    flex-direction: column; }

  .multi-border {
    display: none; }

  .search__content {
    @include padding(null null 10px 22px );
    font-size: 15px;

    .check_unit,
    .radio_unit {
      margin-right: 12px;
      @include padding(10px null); }

    .radio_unit label:before,
    .check_unit .box {
      margin-right: 6px; } }

  .advanced .icon-scan {
    @include size(150px, 50px);
    &:before {
      font-size: 20px; } } }

@include max(350) {
  .search__title {
    @include padding(null null 10px null ); }

  .search__content {
    @include padding(null null null 16px);
    font-size: 14px;

    .check_unit,
    .radio_unit {
      margin-right: 10px; } } }


.result-bottom {
  position: relative;
  .icon-scan {

    @include size(200px, 60px);
    @include position(absolute, -15px null null 27px);
    @include hoverBgc($greenL);

    &:before {
      margin-right: 10px;
      font-size: 22px; } }

  @include max(1200) {
    .icon-scan {
      position: static;
      display: flex;
      @include margin(null auto); } }

  @include max(450) {
    .icon-scan {
      @include size(150px, 50px);
      &:before {
        font-size: 20px; } } } }








