

.user-menu {
  background-color: $green;

  position: relative;

  @include min(768) {

    .toggle-cover {
      @include fc;
      text-align: center;
      max-width: 1440px;
      @include margin(null auto);
      @include padding(null 20px); }
    a {
      color: #fff;
      // width: 280px
      width: 100%;

      @include padding(18px);
      border-right: 1px solid #fff;
      display: inline-block;
      transform: perspective(1px); }

    a:first-child {
      border-left: 1px solid #fff; }
    a:after {
      content: "";
      display: block;
      z-index: -1;
      @include position(absolute, 0 0 0 0);
      transform: scaleY(0);
      transform-origin: 50% 0;
      background-color: $greenXL;
      transition: transform 0.3s;
      transition-timing-function: ease-out; }

    a:hover {
      &:after {
        transform: scaleY(1);
        transition: transform 0.3s; } }

    a.active {
      &:after {
        transform: scaleY(1); }

      &:before {
        content: "";
        position: absolute;
        @include size(20px);
        background-color: #06880a;
        transform: translateX(-50%) rotate(45deg);
        bottom: -8px;
        left: 50%; } } }


  @include max(767) {
    .toggle-nav {
      @include margin(null auto);
      width: 80%;
      border-right: 1px solid #fff;
      border-left: 1px solid #fff;
      height: 60px;
      position: relative;
      @include fc;

      &:before {
        font-weight: bold;
        content: "目錄";
        display: block;
        color: #fff; } }


    .toggle-cover {
      z-index: 6;
      @include position(absolute, 60px null null 10%);
      width: 80%;
      display: none; }

    a {
      @include size(100%, 50px);
      color: #fff;
      background-color: rgba($greenXL, 0.9);
      @include fc;

      border-top: 1px solid rgba(#fff,0.3); }

    a.active {
      background-color: #016d00; } }

  @include max(450) {
    .toggle-nav {
      height: 50px; }

    .toggle-cover {
      top: 50px;
      a {
        font-size: 15px; } } } }


.user-main.cancel > img {
