.returning {
  .hide {
    display: none; }

  table.plain.cart {

    th:first-child {
      @include padding(null 15px);

      @include min(1366) {
        min-width: 110px; } }

    .check_unit {
      justify-content: center;
      .box {
        margin: 0 0 0 15px;
        position: relative;
        transform: scale(1.1);
        border: 1px solid $green;
        border-radius: 2px;

        &:before {
          right: 7px;
          transition: 0s; }
        &:after, {
          right: 5px;
          transition: 0s; } } } }



  .spe1 {
    width: 100%;

    select {
      width: 100%;
      max-width: 300px; } }

  table.column {
    .row {
      height: auto; }

    .radio-inline {
      height: 60px;
      flex-wrap: wrap; } }

  table.column.spe2 {
    th {
      padding-left: 75px;
      padding-right: 0px; }

    .row {
      margin-bottom: 0;
      label {
        width: 100%;
        justify-content: flex-start; }
      input {
        max-width: 280px;
        width: 100%; }
      select {
        max-width: 480px;
        width: 100%;
        margin-right: 30px; } } }

  .multi {
    .red {
