        justify-content: flex-start; }

      .hide {
        display: block;
        margin-left: 10px; } } }

  @include max(850) {

    table.column.spe2 {
      th {
        padding-left: 40px; }

      .row {
        flex-wrap: wrap;
        label {
          margin-right: 0; }
