

.user-main {

  // .btn.lg
  //   min-width: 150px


  .icon-mail {
    font-size: 55px;
    font-weight: bold;
    border-radius: 50%;
    border: 5px solid #eee;
    @include size(120px);
    padding-bottom: 3px;
    @include fc;
    @include margin(null auto); } }


table.column {
  max-width: 800px;
  width: 100%;
  border: 1px solid #09a30d;
  @include margin(null auto);

  tr {
    @include hoverBgc(#f3f3f3);
    &:not(:last-child) {
      border-bottom: 1px solid #cdcdcd; } }

  td,
  th {
    height: 60px;
    text-align: left;
    @include padding(18px);
    font-weight: bold; }

  th {
    background-color: #f0f0f0;
    white-space: nowrap;
    width: 140px;
    &.pad {
      vertical-align: top;
      line-height: 40px; }

 }    // +padding(24px null)

  &.column-sm {
    max-width: 540px; }

  @include max(991) {
    font-size: 15px; }


  &.lg {
    max-width: 100%;
    td,
    th {
      @include padding(null 35px); }
    th {
      background-color: transparent; } }


  select,
  input:not([type="radio"]),
  textarea {
    @include size(100%, 40px);
    background-color: #e6f5e7;
    @include padding(null 20px);
    border: 1px solid $green;
    border-radius: 10px;
    // line-height: 40px
    @include trans;
    font-weight: bold;
    &::placeholder {
      color: #999;
 }      // font-weight: bold
    &:focus {
      border-color: #028900; } }

  textarea {
    height: auto;
    @include padding(12px null ); }
  select {
    background: url(./images/tri-arrow.png) center right 15px no-repeat, #e6f5e7;
    width: 135px;
    @include trans;

    &::-ms-expand {
      display: none; } }

  .row {
    justify-content: flex-start;
    margin-bottom: 20px;
    input {
      max-width: 68px; }
    //   flex: 0 0 68px

    label {
      margin-right: 15px;
      @include fc;
      display: inline-flex; }
    span {
      margin-right: 10px;
      white-space: nowrap; } }

  @include max(991) {
    select {
      width: 100px;
      background-size: 10px;
      background-position: center right 10px; } }

  @include max(700) {
    th {
      @include padding(15px null);
      font-size: 14px;
      width: auto; }
    td {
      @include padding(15px null);
      font-size: 14px;
      height: auto; }

    input,
    select {
      @include padding(null 6px); }

    .row {
      margin-bottom: 0;
      flex-wrap: wrap;
      span {
        width: 60px; }

      label {
        margin-bottom: 10px; } } }

  @include max(510) {
    th,
    td {
      @include padding(12px); }

    &.lg {
      th,
      td {
        @include padding(null 25px); } }


    .row {
      select,
      input {
        width: calc(100% - 60px);
        max-width: calc(100% - 60px); }

      label {
        margin-right: 0;
        width: 100%; } } } }



.container-w800 {
  max-width: 800px;
  @include margin(null auto);

  // table.column
  //   margin-bottom: 20px

  .btn-container-edit {
    text-align: right;

    .btn {
      width: 80px;
      background-color: #fff;
      color: $green;
      border: 1px solid $green;
      @include hoverText(#fff);
      @include hoverBgc($green); }

    .btn:last-child {
      margin-left: 20px; } } }


.user-main.bonus {

  .info {
    @include f;
    .block {
      white-space: nowrap; }
    .g {
      font-size: 24px;
      font-family: roboto;
      @include margin(null 5px); }
    .pipe {
      color: #aaa;
      @include margin(null 12px);
      position: relative;
      bottom: 2px; }

    @include max(900) {
      flex-wrap: wrap;
      .block:nth-child(2) {
        margin-right: calc(100% - 500px);

        .pipe {
          display: none; } } }

    @include max(450) {
      .block {
        margin-right: 0;
        width: 100%; }
      .g {
        font-size: 20px; }
      .pipe {
        display: none; } } }



  table.plain {
    font-weight: bold;
    th,
    td {
      &:nth-child(1) {
        .robo {
          font-size: 20px;
          @include max(450) {
            font-size: 16px; } } }

      &:nth-child(2) {
        text-align: left;
        width: 30%;
        a {
          @include margin(null 5px); } }

      @include min(1600) {
        &:nth-child(1) {
          width: 330px; }
        &:nth-child(2) {
          width: 30%; } } } } }


