

.row {
  @include fc;
  width: 100%; }



.yearLog_select {
  margin-right: 20px;
  width: 250px;
  height: 50px;
  position: relative;
  white-space: nowrap;

  border-radius: 5px;
  overflow: hidden;
  border: 1px solid $green;

  @include f(flex-start, center);
  &:after {
    content: "\f004";
    display: block;
    @include position(absolute, 14px 14px null null );  //20-4
    pointer-events: none;
    font-family: "fontello";
    color: $green; }

  > span {
    background-color: $green;
    color: #fff;
    flex-shrink: 0;
    @include size(100px, 100%);
    @include fc;
    @include padding(null 15px); }



  input[type="month"] {
    border: none;
    &::-webkit-inner-spin-button,
    &::-webkit-clear-button {
      display: none; }
    &::-webkit-calendar-picker-indicator {
      opacity: 0; }

    @include size(100%);
    font-size: 15px;
    background-color: #e6f5e7;
    @include padding(null 10px);
    @include trans;

    &:hover {
      background-color: rgb(255, 255, 255); } } }

.yearLog__button {
  background-color: $green;
  color: #fff;
  @include size(150px ,50px);
  border-radius: 10px;
  border: none;
  cursor: pointer; }

.yearLog__button,
.yearLog_select span {
  @include hoverBgc($greenL); }


.yearLog {
  @include max(680) {
    .row {
      flex-wrap: wrap; }
    .yearLog_select {
      width: 80%;
      @include margin(null 0 20px); } }

  @include max(350) {
    .yearLog__button,
    .yearLog_select {
      height: 45px;
      border-radius: 5px; }

    .yearLog_select {
      width: 100%;
      @include margin(null null 15px); } } }




// ========================================================
.cards {
  // +container
  @include f;
  justify-content: center;
  flex-wrap: wrap;
  align-items: stretch;

  &.minun {
    @include margin(null -12px); }

  .card__pad {
    // max-width: 300px
    flex: 1 1 250px;
    @include padding(null 12px);
    margin-bottom: 60px; }

  .card {
    white-space: normal;

    @include fcl;
    align-items: stretch;
    height: 100%;
    border: 1px solid #ccc;
    text-align: center;
    border-radius: 8px;
    overflow: hidden; }


  .card__text {
    @include padding(15px 36px 15px);
    margin-bottom: auto; }

  .card__price {
    @include padding(null null 20px);
    font-family: "Roboto";

    span:nth-child(2) {
      @include margin(null 3px);
      color: #ccc; }

    .original {
      color: #aaa;
      text-decoration: line-through; } }

  .card__cart {
    @include size(100%, 80px);
    @include fc;
    background-color: $grayBgc;
    cursor: pointer;
    position: relative;
    transform: perspective(1px) translateZ(0);
    @include hoverText($green);
    &:before {
      content: "";
      position: absolute;
      z-index: -1;
      left: 0;
      right: 100%;
      bottom: 0;
      background: $green;
      height: 4px;
      -webkit-transition-property: right;
      transition-property: right;
      -webkit-transition-duration: 0.3s;
      transition-duration: 0.3s;
      -webkit-transition-timing-function: ease-out;
      transition-timing-function: ease-out; } }

  .card:not(.disabled):not(.replenish) {
    .card__cart:hover {
      &:before {
        right: 0; } } }

  .card__pad.push {
    margin-bottom: 0; }


  transition: 0.3s box-shadow;
  .card:not(.disabled):not(.replenish):hover {
    transition: 0.3s box-shadow;
    box-shadow: 0 5px 15px -3px rgba(black, 0.3); } }

.disabled,
.replenish {
  position: relative;
  &:before {
    content: "";
    display: block;
    @include size(100%);
    position: absolute;
    background-color: rgba(#eee, 0.4); }

  .card__cart {

    span,
    .icon-cart {
      display: none; }

    &:after {
      font-size: 20px;
      cursor: default;
      color: $red;
      display: block;
      z-index: 1;
      position: relative; } } }


.disabled .card__cart:after {
  content: "已停售"; }
.replenish .card__cart:after {
  content: "補貨中"; }

.card__img {
  flex-shrink: 0; }

.cards {
  @include max(1366) {
    .card__pad {
      // max-width: 270px
      flex: 1 1 180px;
      margin-bottom: 40px;
      @include padding(null 10px); }

    .card__img {
      width: 90%;
      @include margin(null auto); }

    .card__text {
      @include padding(15px 25px 15px); }
    .card__price {
      padding-bottom: 10px; }

    .card__cart {
      height: 60px; } }

  @include max(600) {
    @include padding(null 10px);
    .card__pad {
      // max-width: 200px
      flex: 1 1 140px;
      margin-bottom: 30px;
      @include padding(null 6px); }

    .card__img {
      width: 80%; }

    .card__text {
      @include padding(5px 12px 10px);
      font-size: 14px; }


    .card__cart {
      height: 55px; }

    .disabled,
    .replenish {
      .card__cart:after {
        font-size: 16px; } } }


  @include max(350) {
    .card__pad {
      flex: 1 1 120px; }

    .card__text {
      font-size: 13px; }

    .card__price {
      padding-bottom: 5px;
      .original {
        font-size: 13px; }
      .sale {
        font-size: 16px; } }

    .card__cart span,
    .icon-cart,
    .disabled .card__cart:after,
    .replenish .card__cart:after {
      font-size: 14px; } } }


