


.pages {
  @include f(center, center);
  a {
    display: block;
    font-family: Roboto;
    margin: 0 5px;
    @include f(center, center);
    @include size(35px);
    color: #777;
    border-radius: 50%;

    flex-shrink: 0;
    &:not([class^="icon"]) {
      background-color: #eee; }


    &:hover,
    &.active {
      background-color: $green;
      color: #fff;
      &:before {
        color: #fff; } } }

  @include max(400) {
    a {
      margin: 0 3px;
      font-size: 14px;
      @include size(30px); }

    [class^="icon"] {
      margin: 0; } } }


@mixin afterline($height, $color: rgb(199,199,199)) {
  line-height: $height;
  position: relative;
  &:after {
    content: "";
    display: block;
    border-right: 1px solid $color;
    @include position(absolute, 0 0 null null );
    height: $height; } }


.breadcrumb {
  background-color: $grayBgc;
  white-space: nowrap;

  ul {
    @include padding(25px null);
    border-top: 1px dashed #ccc;
 }    // +f(flex-start, center)

  ul > * {
    height: 50px;
    @include fc;
    display: inline-flex;
    // height: 100%
    vertical-align: middle;
    color: #999;
    font-weight: bolder;
    margin-bottom: 0;
    font-size: 16px;

    &:last-child {
      color: #000; } }

  .title {
    position: relative;
    padding-right: 25px;
    margin-right: 25px;
    color: #000;

    &:after {
      content: "";
      display: block;
      height: 60%;
      border-right: 1px solid #ccc;
      @include position(absolute, 10px 0 null null ); } }

  // .icon-user3
  .hadCircle {
    font-size: 50px; }

  // .icon-cart
  .plusCircle {
    @include size(50px);
    border-radius: 50%;
    background-color: $green;
    @include fc;
    font-size: 25px;
    color: #fff; }


  .icon-right::before {
    margin: 0 12px;
    color: $green; }

  @include max(850) {
    ul > *:nth-child(6) {
      display: flex;
      justify-content: flex-start;
      height: 40px; } }


  @include max(768) {
    ul {
      @include padding(12px null); }

    .hadCircle {
      font-size: 40px; }

    .plusCircle {
      @include size(40px);
      font-size: 20px; } }

  @include max(550) {

    .title {
      margin-right: 0;
      padding-right: 5px;
      span {
        margin-right: 0; }

      &::after,
      span.f28 {
        display: none; } }

    .hadCircle {
      font-size: 27px; }

    .plusCircle {
      @include size(27px);
      font-size: 14px; }

    .icon-right::before {
      @include margin(null 6px);
      font-size: 12px; }

    ul > * {
      height: 30px;
      font-size: 15px; } }

  @include max(350) {
    ul > * {
      font-size: 14px; } } }


.footGray {
  background-color: #f0f0f0; }

.footGray + footer {
  background-color: #f0f0f0; }


.tel {
  font-family: 'Roboto'; }



footer {
  position: relative;
  padding-top: 120px;
  @include fcl(flex-end);
  letter-spacing: 1px;

  .icon-up {
    @include size(60px);
    background-color: rgb(239, 239, 239);
    color: $green;
    position: absolute;
    top: 30px;
    right: 35px;
    border: 1px solid $green;
    border-radius: 50%;
    @include fc;
    font-size: 32px; }

  svg {
    @include position(absolute, null 0 0 null); }

  &,a {
    color: #fff;
    transition: 0.3s text-shadow; }



  .sitemap,
  address {
    a:hover {
      transition: 0.3s text-shadow;
      text-shadow: 0 0 10px rgb(12, 102, 13) , 0 0 20px #fff; } }



  .copyright {
    @include fc;
    border-top: 1px solid rgba(#fff,0.3);
    width: 100%;
    z-index: 1;
    min-height: 60px;
    @include padding(15px);
    .slash {
      @include margin(null 10px); } }


  .wrapper {
    @include f(space-around, flex-start);
    margin-bottom: 20px;
    z-index: 1; }

  address {
    @include fcl;
    padding-right: 20px;
    align-items: flex-start;
    li {
      padding-bottom: 8px; } }

  .media {
    @include fcl;
    margin-left: 30px; }

  .icon-facebook,
  .icon-line {
    @include size(60px);
    font-size: 30px;
    background-color: #fff;
    border-radius: 50%;
    color: $green;
    @include fc;
    @include margin(null 8px 16px); }


  .sitemap {
    @include f(center, flex-start);
    flex-shrink: 0;

    .title {
      font-size: 20px;
      padding-bottom: 10px;
      margin-bottom: 12px;
      border-bottom: 1px solid rgba(#fff, 0.3);
      font-weight: bold; } }

  .block:not(:last-child) {
    margin-right: 50px; }

  .block.tri ul {
    padding-right: 20px;
    max-width: 420px;
    @include f(space-between,center, row, wrap);

    li:nth-child(3n+1) {
      width: 25%; }
    li:nth-child(3n+2) {
      width: 35%; }
    li:nth-child(3n+3) {
      width: 33%; } } }


footer {

  @include max(1365) {
    padding-top: 70px;

    .sitemap {
      display: none; }

    .wrapper {
      @include f(space-around, flex-start);
      flex-direction: column;
      align-items: center; }

    .media {
      flex-direction: row;
      margin-left: 0; }

    [class^="icon"] {
      margin-bottom: 0;
      @include size(50px);
      font-size: 25px; }

    address {
      @include padding(null 0 0px 20px); } }

  @include max(768) {



    .icon-up {
      top: -10px; }
    .copyright {
      flex-direction: column;
      span {
        margin-bottom: 5px;
        text-align: center; } }

    .slash {
      display: none; } }

  @include max(350) {
    address {
      @include padding(null null null 0); }
    [class^="icon"] {
      @include size(45px);
 } } }      // font-size: 20px
