
.catalog {
  @include f;
  position: relative; }

.catalog__left {
  padding-right: 300px;
  width: 100%; }


.filter {
  display: inline-block;
  @include f;

  a {
    flex: 0 1 210px;
    height: 60px;
    &:first-child {
      border-radius: 8px 0 0 8px; }
    &:last-child {
      border-radius: 0 8px 8px 0; }

    &:not(:last-child) {
      border-right: 1px solid #ccc; }

    background-color: #efefef;
    @include fc;
    display: inline-flex;
    position: relative;
    .tri {
      @include position(absolute, null null -100% 40% );
      @include size(28px, 100%);
      overflow: hidden;

      &:after {
        @include size(0);
        border-style: solid;
        border-width: 27px 20px 0 8px;
        border-color: $green transparent transparent transparent;
        content: "";
        @include position(absolute, -28px null null null );
        transition: 0.3s top; } }

    &.active {
      background-color: $green;
      color: #fff;
      .tri:after {
        transition: 0.3s top;
        top: 0; } }


    transition: 0.3s;
    &:hover {
      background-color: $green;
      color: #fff;
      .tri:after {
        transition: 0.3s top;
        top: 0; } } }

  @include max(600) {
    flex-wrap: wrap;
    justify-content: center;

    a {
      width: 50%;
      flex: 0 0 auto;

      &:first-child {
        border-radius: 0; }
      &:last-child {
        border-radius: 0; }
      .tri {
        display: none; }

      &:nth-child(1) {
        border-bottom: 1px solid #ccc; }
      &:nth-child(2) {
        border-right: 0px solid #ccc;
        border-bottom: 1px solid #ccc; } } }

  @include max(380) {
    a {
      height: 45px;
      font-size: 14px; } } }


.catalog__menu {
  position: absolute;
  right: 25px;
  top: 0;
  width: 265px;

  a {
    width: 100%;
    border-bottom: 1px dashed #ccc;
    @include padding(17px null);
    @include hoverBgc(#e6f6e6); }

  .dropdwon a {
    @include padding(null 10px);
    &:before {
      @include size(12px);
      content: "";
      display: inline-block;
      background-color: $green;
      margin-right: 15px;
      border-radius: 50%; } }

  .icon-double-right {
    @include f;
    border-top: 1px dashed #ccc;
    align-items: center;
    span {
      @include margin(null 2px); }
    &:before {
      font-size: 30px;
      margin-right: 3px; } } }


@include max(1100) {

  .catalog__left {
    padding-right: 0; }

  .catalog__menu {
    position: static;
    .icon-double-right {
      border-width: 0px;
      @include hoverBgc(#fff);
 }      // padding-bottom: 0

    .dropdwon {
      display: none;
      @include position(absolute, null null null 0);
      box-shadow: 0 3px 6px rgba(#000,0.3);
      background-color: #fff;
      z-index: 1;
      width: 100%;
      padding-bottom: 20px;
      z-index: 6;

      a {
        width: calc(20% - 36px);
        @include margin(null 18px); }

      @include max(800) {
        a {
          width: calc(25% - 36px); } }
      @include max(650) {
        a {
          width: calc(33% - 36px);
          @include padding(12px null);
          font-size: 15px;
          &:before {
            @include size(8px);
            margin-right: 6px; } } }

      @include max(420) {
        @include padding(null 15px null null);
        a {
          font-size: 14px;
          &:before {
            display: none; } } } } } }
