
.index_landing {
  height: 40vw;
  min-height: 400px;
  background: center / cover;
  @include f(center, flex-end);
  position: relative;

  a {
    // +size(380px, 185px)
    @include padding(38px 54px);
    @include margin(null 15.6vw 4.2vw );
    background-color: #fff;
    color: $green;
    border-radius: 999px;
    @include fc;
    flex-shrink: 0;
    font-weight: bold;
    box-shadow: 0 5px 5px rgba(black,0.1);
    z-index: 1; }

  [class*="icon"] {
    flex-shrink: 0;
    font-size: 4.5rem;
    &:before {
      width: auto;
      margin-right: 0.2em; } }


  .main {
    font-size: 40px;
    & + div {
      font-size: 20px; } }

  [class^="man"] {
    position: absolute;
    bottom: 0; }

  .man-m {
    left: 45%;
    transform: translateX(-50%);
    width: 17.7vw; }
  .man-f {
    width: 14.2vw;
    right: 45%;
    transform: translateX(50%); }

  [class^="plus"] {
    &:before,&:after {
      content: "";
      display: block; }
    &:after {
      transform: rotate(90deg); } }

  .plus-1 {
    &:before, &:after {
      @include size(30px, 10px);
      @include position(absolute, null 49% 120px null );
      background-color: rgb(56, 175, 59);
 } }      // background-color: red

  .plus-2 {
    &:before, &:after {
      @include size(30px, 10px);
      @include position(absolute, null 38% 130px null );
      background-color: #51c34d; } }

  .plus-3 {
    &:before, &:after {
      @include size(30px, 10px);
      @include position(absolute, null 65% 60px null );
      background-color: #62b664; } }

  .plus-4 {
    &:before, &:after {
      @include size(30px, 10px);
      @include position(absolute, null 60% 190px null );
      background-color: #62b664;
      background-color: {} } }



  @include max(1440) {
    a {
      @include padding(25px 40px); } }

  @include max(1366) {
    a {
      @include padding(24px 30px); }

    [class*="icon"] {
      font-size: 3rem; }

    .main {
      font-size: 30px;
      & + div {
        font-size: 16px; } } }

  @include max(991) {
    [class^="man"] {
      display: none; }
    a {
      @include margin(null 3vw 40px); } }

  @include max(767) {
    @include f(flex-end, center, column);
    a {
      @include padding(15px 25px); }

    [class*="icon"] {
      font-size: 2.5rem; }

    .main {
      font-size: 18px;
      & + div {
        font-size: 15px; } } } }



@include min(769) {


  #nav .grayInner .moblie,
  .index_landing a {
    transition: 0.3s box-shadow , 0.3s transform; }

  #nav .grayInner .moblie,
  .index_landing a {
    &:hover {
      transition: 0.3s box-shadow , 0.3s transform;
      transform: translateY(1.5px);
      [class*="icon"]:before {
        animation: hrv 1s; } } }

  .index_landing a {
    &:hover {
      box-shadow: 0 0px 20px rgba(white, 0.3); } }

  @keyframes hrv {
    16.65% {
      transform: translateY(4px); }
    33.3% {
      transform: translateY(-3px); }
    49.95% {
      transform: translateY(3px); }
    66.6% {
      transform: translateY(-1.5px); }
    83.25% {
      transform: translateY(0.5px); }
    100% {
      transform: translateY(0); } } }



