.index_search {
  position: relative;

  .searchbar {
    @include fc;
    position: relative; }

  .dot {
    @include size(20px);
    background-color: $blue;
    border-radius: 50%;
    margin-right: 20px; }

  .dot ~ * {
    @include padding(20px 28px);
    font-size: 20px;
    height: 70px; }

  input[type="search"] {
    width: 30vw;
    border: 1px solid $green;
    border-radius: 10px 0 0 10px;
    font-weight: bold;
    @include hoverBgc(tint($green, 95%));

    &, &::placeholder {
      color: $blue; }

    &:focus {
      border-width: 2px; } }


  .search,
  .advanced {
    @include fc;
    color: #fff;
    background-color: $green;
    @include hoverBgc(tint($green, 20%));

    &:before {
      font-size: 25px;
      margin-right: 10px; } }

  .search {
    width: 140px;
    border-radius: 0 10px 10px 0;
    border-width: 0px;
    margin-right: 16px;
    cursor: pointer; }

  .advanced {
    width: 180px;
    border-radius: 10px; }

  .tips {
    // padding-bottom: 50px
    font-size: 20px;
    color: $green;
    @include fcl;
    font-weight: bold; }

  .multi {
    @include fc;
    margin-bottom: 20px; }

  .circle {
    @include size(75px);
    border-radius: 50%;
    @include fc;
    color: #FFF;
    @include margin(null 12px);

    &:first-child {
      background-color: $blue; }
    &:last-child {
      background-color: $orange; } }

  // [class*="icon"]:before
  //   color: #fff

  @include max(1365) {
    .dot ~ * {
      @include padding(15px 20px);
      font-size: 18px;
      height: 60px; }

    input[type="search"] {
      @include padding(null 10px);
      font-weight: normal; }

    .search,
    .advanced {
      width: auto;
      &:before {
        font-size: 18px; } }

    .tips {
      font-size: 18px; }

    .circle {
      @include margin(null 6px); } }

  @include max(767) {

    @include padding(null 20px);
    .dot {
      display: none; }

    .searchbar {
      max-width: 400px;
      @include margin(null auto);
      flex-wrap: wrap;
      justify-content: flex-end; }

    .dot ~ * {
      @include padding(0px 10px);
      font-size: 16px;
      height: 55px; }


    input[type="search"] {
      font-size: 15px;
      @include padding(null 5px);
      font-weight: normal;
      -webkit-appearance: none;
      width: 60%; }

    .search, .advanced {
      width: 40%;
      margin-right: 0; }

    .advanced {
      margin-top: 15px;
      width: 100%; }

    .tips {
      font-size: 15px; }

    .multi {
      flex-wrap: wrap;
      font-weight: normal; }

    .circle {
      @include size(auto);
      @include margin(null 3px);

      &:first-child {
        color: $blue;
        background-color: transparent; }
      &:last-child {
        color: $orange;
        background-color: transparent; }
      &:before {
        content: attr(data-inline); }
      span {
        display: none; } } } }

