.division {
  @include f;
  flex-wrap: wrap;
  align-items: stretch;

  .block {
    @include margin(null 12px 24px 12px );
    width: calc(20% - 24px);
    @include padding(30px 25px);
    @include fcl;
    display: inline-flex;
    border-radius: 8%;
    background-color: #fff;
    transition: box-shadow 0.3s;
    img {
      flex-shrink: 0; }

    &:hover {
      box-shadow: 0 0 1px 1px $green;
      transition: box-shadow 0.3s; } }

  .title {
    margin-top: 8px;
    text-align: center;

    &::before {
      @include size(18px);
      content: "";
      display: inline-block;
      background-color: $orange;
      margin-right: 15px;
      border-radius: 50%; } }



  @include max(1365) {
    .block {
      font-size: 18px; }

    .title:before {
      @include size(12px);
      margin-right: 10px; } }

  @include max(1100) {
    .block {
      @include margin(null 8px 16px 8px );
      width: calc(25% - 16px);
      @include padding(20px 20px); } }


  @include max(991) {
    .title:before {
      display: none; } }

  @include max(767) {
    .block {
      width: calc(33% - 10px);
      @include margin(null 5px 10px 5px); }
    .title {
      margin-top: 0;
      span {
        display: block; } } }

  @include max(500) {
    @include padding(20px 12px);
    .block {
      font-size: 14px;
      @include padding(8px 15px); } } }


