.subdivision {
  background-color: #fff; }

.subdivision__land {
  @include f;
  img {
    width: 260px; }
  .text {
    max-width: 530px; }

  @include max(600) {
    flex-direction: column;
    align-items: center;
    img {
      max-width: 250px;
      width: 80%;
      margin-right: 0; }


    .text {
      max-width: 400px;
      .f24 {
        margin-top: 20px; } } } }

.subdivision__list {
  border-top: 1px solid #ccc;

  a {
    width: calc(20% - 36px);
    @include margin(null 18px);
    border-bottom: 1px dashed #ccc;
    @include padding(20px null);

    &:before {
      @include size(14px);
      content: "";
      display: inline-block;
      background-color: $green;
      margin-right: 20px;
      border-radius: 50%; }

    @include hoverText($red); }

  @include max(1024) {
    a {
      width: calc(25% - 36px);
      @include padding(15px null);
      &:before {
        @include size(12px);
        margin-right: 15px; } } }

  @include max(700) {
    a {
      width: calc(33% - 36px);
      @include padding(12px null);
      font-size: 15px;
      &:before {
        @include size(8px);
        margin-right: 6px; } } }

  @include max(420) {
    @include margin(null -5px null -10px);
    a {
      font-size: 14px;
      &:before {
        display: none; } } } }


#catalog-anchor {
  position: absolute;
  @include size(1px);
  top: -70px; }
